	cursor:default;
	-webkit-box-shadow:0 0 5px rgba(0,0,0,.8);
	        box-shadow:0 0 5px rgba(0,0,0,.8);
}

.referenceLink {
	padding:0px 5px;
	font-family:"Helvetica Neue",Helvetica,Arial,sans-serif;
	height:35px;
	bottom:10px;
	top:initial;
	right:35px;
	position:absolute;
	background-color:#e02e0b;
	position:fixed;
	color:white;
	font-size:24px;
	line-height:35px;
	cursor:pointer;
	-webkit-box-shadow:0 0 5px rgba(0,0,0,.8);
	        box-shadow:0 0 5px rgba(0,0,0,.8);
}

.fixed{
    position: fixed;
    left: 50%;
